import type { Meta, StoryFn } from '@storybook/web-components';

import { html } from 'lit';

import { FieldSize } from '@tiger-analytics/ui';

export default {
  title: 'Form/ta-checkbox',
  component: 'ta-checkbox',
  parameters: {
    controls: { expanded: true },
  },
  argTypes: {
    id: {
      description: 'Mandatory - unique id for testing',
      table: {
        defaultValue: { summary: '' },
      },
      control: 'text',
    },
    label: {
      description: 'Label for the checkbox',
      table: {
        defaultValue: { summary: '' },
      },
      control: 'text',
    },
    description: {
      description: 'Additional description for the field',
      table: {
        defaultValue: { summary: '' },
      },
      control: 'text',
    },
    checked: {
      description: 'Whether the checkbox is checked or not',
      table: {
        defaultValue: { summary: 'false' },
      },
      control: 'boolean',
    },
    disabled: {
      description: 'Enable or disable the checkbox',
      table: {
        defaultValue: { summary: 'false' },
      },
      control: 'boolean',
    },
    required: {
      description: 'Mark checkbox as required',
      table: {
        defaultValue: { summary: 'false' },
      },
      control: 'boolean',
    },
    size: {
      options: [FieldSize.Default, FieldSize.Small],
      description: 'Size of the checkbox',
      table: {
        defaultValue: { summary: FieldSize.Default },
      },
      control: { type: 'select' },
    },
    change: {
      description: 'Triggered when the checkbox value is changed',
      table: {
        category: 'Events',
      },
      control: false,
    },
    focus: {
      description: 'Focuses the checkbox',
      table: {
        category: 'Methods',
      },
      control: false,
    },
  },
} as Meta;

const description = `ta-checkbox is a custom checkbox input.`;

const Template: StoryFn = (args) => {
  const checkboxChangeHandler = (event: Event) => {
    const checkboxTarget = event.target as HTMLInputElement;
    const outputElement = document.getElementById('checkbox-status');
    if (outputElement) outputElement.innerHTML = checkboxTarget.checked ? 'Checked' : 'Unchecked';
  };
  return html`
    <ta-grid-container .showSideMargin="${false}">
      <ta-grid-column>
        <ta-checkbox
          id="${args.id}"
          .label="${args.label}"
          .size="${args.size}"
          ?disabled="${args.disabled}"
          .description="${args.description}"
          .required="${args.required}"
          @change="${(e: Event) => checkboxChangeHandler(e)}"></ta-checkbox>
        <p>Status: <span id="checkbox-status"></span></p>
      </ta-grid-column>
      <ta-grid-column>
        <div class="story-title">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div>
      </ta-grid-column>
      <ta-grid-column>
        <div class="story-description">${description}</div>
        <div class="story-description">
          <b>Note :</b>To group multiple input fields, fieldset should be used.
          <a
            href="https://developer.mozilla.org/en-US/docs/Web/HTML/Element/fieldset"
            target="_blank"
            >Read more about fieldset</a
          >
        </div>
      </ta-grid-column>
    </ta-grid-container>
  `;
};

export const CodeAndAccessibility = Template.bind({});
CodeAndAccessibility.args = {
  id: 'test-checkbox',
  label: 'Accept Terms',
  size: FieldSize.Default,
  disabled: false,
  description: 'Please check this box to agree to the terms and conditions.',
  checked: false,
  required: false,
};
